@use "global" as *;

/* Loading背景画面設定　*/
.splash{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #D6E6F5; /* 直接カラーコードに修正 */
  text-align: center;
  color: #fff;
  display: flex; /* これを追加（中央配置のため） */
  justify-content: center;
  align-items: center;
}


/* Loading アイコンの大きさ設定　*/
.splash__logo img {
  width:rem(260);
}

/* fadeUpをするアイコンの動き */
.fade-up{
  animation: fade-up 0.5s ease-out forwards;
	opacity: 0;
}

@keyframes fade-up{
  0% {
  opacity: 0;
  transform: translateY(100px);
  }

  100% {
  opacity: 1;
  transform: translateY(0);
  }
}
